
body,html{
	/*background-color: white;*/
}
.order{
		width: 100%;
		height: 100%;
		/*background-color: white;*/
}
.sele_bar{
    	width: 100%;
    	background-color: white;
    	display: -ms-flexbox;
    	display: flex;
    	box-shadow: 0px 2px 0 0 gainsboro;
    	background-color: #EBC86F;
    	color: white;
}
.sele_bar div{
    	width: 50%;
    	text-align: center;
}
.active{
    	color: white;
    	border-bottom: 2px solid white;
    	padding-bottom: 5px;
}
.active_1{
    	width: 95%;
    	background-color: white;
    	margin: auto;
    	margin-top: 8px !important;
    	border-radius:8px;
}
.ac_header{
    	height: 48px;
    	line-height: 48px;
    	border-bottom: 1px solid rgba(232,232,232,1);
}
.ma{
    	width: 95%;
    	margin: 0 0px 0 10px;
}
.ac_center{
    	display: -ms-flexbox;
    	display: flex;
    	border-bottom: 1px solid gainsboro;
}
.ac_center img{
    	width: 88px;
    	margin: 12px;
    	margin-left: 0;
    	height: 88px;
}
.s_p1{
    	
		font-size:16px;
		font-family:PingFangSC-Regular;
		font-weight:400;
		color:rgba(102,102,102,1);
		line-height:22px;
		display: inline-block;
		margin-top: 12px;
}
.ac_bottom{
    	height: 48px;
    	line-height: 48px;
    	display: -ms-flexbox;
    	display: flex;
    	width: 100%;
    	position: relative;
    	text-align: left;
}
.btn_imm{
    	width:80px;
		height:32px;
		background:rgba(235,200,111,1);
		border-radius:4px;
		/*border:1px solid rgba(235,200,111,1);*/
		color: white;
		line-height: 32px;
		float: right !important;
		margin-top: 8px;
		/*margin-left: 91px;*/
		/*float: right;*/
		position: absolute;
		right: 0;
}



