
.sele_bar {
	width: 100%;
	background-color: white;
	display: -ms-flexbox;
	display: flex;
	box-shadow: 0px 2px 0 0 gainsboro;
	z-index: 50;
	margin-bottom: 2px;
}
.sele_bar div {
	margin: 15px 20px 15px 20px;
}
.active {
	color: #EBC86F;
	border-bottom: 2px solid #EBC86F;
	padding-bottom: 5px;
	width: 10px;
}
.active_2 {
	width: 100%;
	background-color: white;
	margin: auto;
	/*margin-top: 2px;*/
	z-index: 1;
	border-bottom: 1px solid rgba(232, 232, 232, 1);
}
.active_2:last-child {
	border-bottom: none !important;
}
.ac_header {
	height: 48px;
	line-height: 48px;
}
.ma {
	width: 95%;
	margin: 0 0px 0 10px;
}
.ac_center1 {
	display: -ms-flexbox;
	display: flex;
	/*border-bottom: 1px solid gainsboro;*/
}
.ac_center1 img {
	width: 116px;
	margin: 12px;
	margin-left: 0;
	height: 116px;
}
.s_p {
	font-size: 16px;
	font-family: PingFangSC-Regular;
	font-weight: 400;
	color: rgba(102, 102, 102, 1);
	line-height: 22px;
	display: inline-block;
	margin-top: 12px;
}
.div_bar{
	width: 100%;
	position: relative;
}
.div_bar button {
	width: 48px;
	height: 25px;
	border-radius: 13px;
	border: 1px solid rgba(235, 200, 111, 1);
	font-size: 14px;
}
.list_bar {
	position: fixed;
	right: 13px;
	z-index: 6666;
	top: 0;
	height: 50px;
	line-height: 55px;
}
.list_bar img {
	width: 21px;
}
.add_new {
	width: 124px;
	height: 48px;
	background: rgba(235, 200, 111, 1);
	border-radius: 24px;
	color: white;
	position: fixed;
	margin: auto;
	z-index: 55;
	left: calc(50% - 62px);
	bottom: 65px;
}
.big_width {
	width: 155px;
	height: 12px;
	background: papayawhip;
	border-radius: 8px;
	/*opacity:0.3;*/
	margin-top: 8px;
	position: relative;
}
.small_width {
	width: 75px;
	height: 12px;
	background: rgba(235, 200, 111, 1);
	border-radius: 8px;
	z-index: 5;
	position: absolute;
	font-size: 10px;
	color: white;
	text-indent: 6px;
}



